___CSS_LOADER_EXPORT___.push([module.id, `/**
 * e-reader theme: APK-inspired color variables (light / default / dark).
 * Use our own values; structure from eReader Prestigio themes.
 */

:root,
[data-theme="light"] {
	--ereader-primary: #607d8b;
	--ereader-primary-text: #fff;
	--ereader-action-bar: #607d8b;
	--ereader-surface: #fff;
	--ereader-background: #f5f5f5;
	--ereader-read-indicator: #607d8b;
	--ereader-fill-button: #607d8b;
	--ereader-shelf-background: linear-gradient(180deg, #e8e8e8 0%, #d8d8d8 100%);
	--ereader-shelf-divider: #c0c0c0;
	--ereader-card-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
	--ereader-text: #212121;
	--ereader-text-secondary: #757575;
	--ereader-border: #e0e0e0;
	--ereader-error: #c62828;
}

[data-theme="default"] {
	--ereader-primary: #a99a6d;
	--ereader-primary-text: #1a1a1a;
	--ereader-action-bar: #f1e3bb;
	--ereader-surface: #faf8f5;
	--ereader-background: #f0ebe0;
	--ereader-read-indicator: #a99a6d;
	--ereader-fill-button: #a99a6d;
	--ereader-shelf-background: linear-gradient(180deg, #e8e2d6 0%, #ddd6c8 100%);
	--ereader-shelf-divider: #c4b89a;
	--ereader-card-shadow: 0 2px 10px rgba(0, 0, 0, 0.08);
	--ereader-text: #2c2c2c;
	--ereader-text-secondary: #5c5c5c;
	--ereader-border: #ddd6c8;
	--ereader-error: #b71c1c;
}

[data-theme="dark"] {
	--ereader-primary: #9e9e9e;
	--ereader-primary-text: #fff;
	--ereader-action-bar: #9e9e9e;
	--ereader-surface: #303030;
	--ereader-background: #212121;
	--ereader-read-indicator: #b0b0b0;
	--ereader-fill-button: #757575;
	--ereader-shelf-background: linear-gradient(180deg, #424242 0%, #373737 100%);
	--ereader-shelf-divider: #555;
	--ereader-card-shadow: 0 2px 12px rgba(0, 0, 0, 0.4);
	--ereader-text: #e0e0e0;
	--ereader-text-secondary: #9e9e9e;
	--ereader-border: #505050;
	--ereader-error: #ef5350;
}

/* App shell and drawer */
#ereader-app-root {
	--ereader-drawer-width: 260px;
	--ereader-app-bar-height: 56px;
	--ereader-radius: 8px;
	--ereader-radius-sm: 4px;
}
